.calendar {
	&__settings {
		margin: 0 -15px 30px;
		font-size: 0;
		@include from-sm {
			text-align: right; }
		@include sm {
			margin-left: -10px;
			margin-right: -10px; } }
	&__select {
		display: inline-block;
		vertical-align: top;
		padding: 0 15px;
		@include sm {
			width: 50%;
			padding-left: 10px;
			padding-right: 10px;
			margin-bottom: 15px;
			.select {
				width: 100%; } }
		@include xs {
			width: 100%; } } }
