.soc {
	font-size: 0;
	li {
		display: inline-block;
		vertical-align: middle;
		a {
			@include from-xs {
				opacity: .6;
				transition: opacity .3s;
				&:hover {
					opacity: 1; } } }
		&:not(:last-child) {
			margin-right: 10px;
			@include sm {
				margin-right: 18px; } }
		.icon {
			font-size: 24px;
			max-width: 28px;
			stroke: #fff;
			@include gpu;
			@include sm {
				font-size: 18px; } }
		.icon-ytb {
			@include sm {
				font-size: 15px; } } } }
