.gallery {
	$this: &;

	position: relative;
	@include from-xs {
		padding-right: 80px; }
	@include xs {
		margin-left: -30px;
		margin-right: -30px; }
	&__item {
		position: relative;
		height: 320px;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		@include xs {
			width: 100%;
			height: 0;
			padding-bottom: 56.25%; }
		&:hover {
			#{$this}__play {
				opacity: 1;
				visibility: visible; } } }
	&__play {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transition: opacity .3s, visibility .3s;
		@include from-xs {
			opacity: 0;
			visibility: hidden;
			&-cover {
				opacity: 1;
				visibility: visible; } } }
	&__play-icon {
		position: absolute;
		bottom: 10px;
		right: 10px;
		@include from-xs {
			display: none; } }
	&__slider-main {
		&:not(.slick-initialized) {
			.slick-slide, #{$this}__item-wrap {
				&:not(:first-child) {
					display: none; } } } }
	&__slider {
		position: relative;
		font-size: 0;
		border-radius: 5px;
		overflow: hidden;
		@include xs {
			border-radius: 0; }
		.slick-vertical .slick-slide {
			border: 0; }
		.slick-list {
			border-radius: 4px;
			@include xs {
				border-radius: 0; } } }
	&__controls {
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		@include xs {
			display: none; } }
	&__arr {
		&:not(:last-child) {
			margin-bottom: 60px; } } }
