.info {
	$this: &;

	position: relative;
	@include pop;
	&__channel {
		position: absolute;
		left: 0;
		top: 0;
		width: 70px;
		@include from-sm {
			min-width: 70px; }
		@include md {
			position: static;
			margin-bottom: 9px; }
		@include sm {
			width: 42px; }
		img {
			max-width: 100%;
			height: auto;
			border-radius: 3px;
			border: 1px solid #1e3a56; } }
	&__body {
		padding-top: 6px;
		padding-left: 100px;
		@include lg {
			padding-left: 85px; }
		@include md {
			padding-left: 0; } }
	&__heading {
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 23px;
		@include xs {
			margin-bottom: 13px; } }
	&__title {
		display: block;
		margin-bottom: 7px;
		color: #fff;
		font-size: 20px;
		@include sm {
			font-size: 16px; }
		&:link {
			transition: color .3s;
			&:hover {
				color: $c-blue; } } }
	&__subtitle {
		color: rgba(#fff, .6);
		@include xs {
			font-size: 13px; } }
	&__text {
		position: relative;
		min-height: 105px;
		padding-bottom: 22px;
		color: rgba(#fff, .6);
		font-size: 14px;
		line-height: 1.5;
		@include xs {
			font-size: 12px; }
		&:not(:last-child) {
			margin-bottom: 15px; }
		&:not(.is-truncuted) {
			#{$this}__more {
				display: none; } }
		&.is-open {
			#{$this}__more {
				transform: rotateX(180deg); } }
		h5 {
			margin-bottom: 5px;
			font-weight: 300;
			color: #fff;
			font-size: inherit;
			letter-spacing: .06em; } }
	&__about {
		position: relative;
		height: 63px;
		overflow: hidden;
		@include gpu;
		@include xs {
			height: 54px; }
		.clone {
			display: none; } }
	&__more {
		position: absolute;
		bottom: 0;
		left: 50%;
		margin-left: -15px;
		width: 30px;
		height: 20px;
		background: none url(data:image/svg+xml;base64,) center center;
		background-position: contain;
		transition: transform .5s; }
	&__progress {
		margin-bottom: 23px; }
	&__buttons {
		> .button {
			min-width: 204px;
			@include r(1200) {
				@include rmin(1024) {
					display: flex;
					width: 100%; } }
			@include xs {
				width: 100%; }
			&:not(:last-child) {
				margin-right: 25px;
				@include r(1200) {
					@include rmin(1024) {
						margin-bottom: 20px;
						margin-right: 0; } }
				@include xs {
					margin-right: 0;
					margin-bottom: 10px; } } } }
	&__buttons_mod {
		margin: 0 -10px -30px;
		font-size: 0;
		@include xs {
			margin-bottom: -20px; } }
	&__button {
		display: inline-block;
		vertical-align: top;
		padding: 0 10px 30px;
		@include xs {
			padding-bottom: 20px; } } }

.info_simple {
	.info {
		&__top {
			display: flex;
			margin-bottom: 50px;
			@include sm {
				flex-direction: column; }
			@include xs {
				margin-bottom: 16px; } }
		&__body {
			flex: 100%;
			@include sm {
				padding-top: 0; } }
		&__text {
			min-height: 0;
			padding-bottom: 0;
			margin-bottom: 0; }
		&__progress {
			margin-bottom: 0; }
		&__channel {
			@include md {
				margin-right: 20px; } } } }
