.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 320px;
  height: 100vh;
  padding: 15px;
  background: rgba(0, 0, 0, 0.2);
  overflow: auto;
  z-index: 999;
  display: none;
  text-align: center;
  font-size: 0;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal__inner {
  display: inline-block;
  vertical-align: middle;
  width: 703px;
  max-width: 100%;
  font-size: 14px;
  text-align: left;
}

.modal__top, .modal__body {
  padding-left: 60px;
  padding-right: 60px;
}

@media only screen and (max-width: 767px) {
  .modal__top, .modal__body {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.modal__top {
  position: relative;
  padding-bottom: 30px;
  margin-bottom: 40px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.3);
}

@media only screen and (max-width: 767px) {
  .modal__top {
    padding-bottom: 15px;
    padding-right: 40px;
    margin-bottom: 25px;
  }
}

.modal__img {
  height: 160px;
  border-radius: 4px;
  background-size: cover;
  background-position: center;
}

@media only screen and (max-width: 767px) {
  .modal__img {
    height: 180px;
  }
}

.modal__title {
  font-size: 24px;
  letter-spacing: .06em;
}

@media only screen and (max-width: 767px) {
  .modal__title {
    font-size: 20px;
  }
}

.modal__close {
  position: absolute;
  top: -7px;
  right: 24px;
}

@media only screen and (max-width: 767px) {
  .modal__close {
    right: 0;
    top: -9px;
  }
}

.modal__row {
  margin: 0 -17px;
}

.modal__row:after {
  content: " ";
  display: table;
  clear: both;
}

.modal__col {
  float: left;
  width: 50%;
  padding: 0 17px;
}

@media only screen and (max-width: 767px) {
  .modal__col {
    width: 100%;
  }
  .modal__col:not(:last-child) {
    margin-bottom: 10px;
