.card {
	display: flex;
	padding-bottom: 40px;
	@include xs {
		padding: 20px 30px;
		margin-left: -30px;
		margin-right: -30px;
		border-top: 2px solid #1e3a56;
		&:nth-child(odd) {
			background: #0f1d2b; } }
	&:not(:last-child) {
		@include from-xs {
			border-bottom: 2px solid #081c26;
			margin-bottom: 40px; } }
	&__img {
		flex: 0 0 275px;
		height: 210px;
		margin-right: 135px;
		background-size: cover;
		background-position: center;
		border-radius: 5px;
		@include lg {
			margin-right: 70px; }
		@include md {
			margin-right: 40px; }
		@include sm {
			flex: 0 0 210px;
			height: 180px; }
		@include xs {
			display: none; } }
	&__info {
		flex: 0 1 480px; } }
