.section {
	padding: 90px 0 60px;
	@include lg {
		padding-top: 40px; }
	@include sm {
		padding-bottom: 40px; }
	&__title {
		margin-bottom: 50px;
		display: flex;
		justify-content: space-between;
		@include lg {
			margin-bottom: 30px; }
		@include sm {
			margin-bottom: 20px;
			align-items: center; }
		@include xs {
			justify-content: flex-start;
			align-items: flex-start;
			flex-direction: column; }
		h2, .h2, h3, .h3 {
			flex: 1 0 auto; } }
	&__meta {
		margin-top: 3px; }
	&__btns {
		margin-top: 63px;
		padding-top: 30px;
		border-top: 2px solid #0e222e;
		@include sm {
			margin-top: 20px;
			padding-top: 20px; }
		.button {
			@include xs {
				width: 100%; }
			&:not(:last-child) {
				@include from-xs {
					margin-right: 26px; }
				@include xs {
					margin-bottom: 10px; } } } }
	&__bottom {
		padding-top: 20px;
		@include from-xs {
			border-top: 2px solid #081c26; }
		@include xs {
			padding-top: 40px; } }
	&__bottom-btn {
		margin-top: 20px; }
	&__settings {
		padding-bottom: 60px;
		@include md {
			padding-bottom: 40px; } }
	&__empty {
		padding-top: 78px; }
	&__sel {
		@include xs {
			width: 100%;
			margin-top: 20px; } }
	&__items {
		margin: 0 -15px;
		font-size: 0;
		@include md {
			white-space: nowrap;
			overflow: auto;
			margin: 0 -20px; }
		@include sm {
			margin: 0 -30px; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		font-size: 14px;
		padding: 0 15px 50px;
		width: 25%;
		@include md {
			width: auto;
			padding-bottom: 20px;
			&:first-child {
				padding-left: 20px; }
			&:last-child {
				padding-right: 20px; }
			.item {
				width: 210px; } }
		@include sm {
			padding: 0 10px 20px;
			&:first-child {
				padding-left: 30px; }
			&:last-child {
				padding-right: 30px; }
			.item {
				width: 193px; } } } }

.section_lg {
	padding-bottom: 80px; }

.section_md {
	padding-bottom: 40px; }

.section_sm {
	padding-top: 80px;
	padding-bottom: 0; }

.section_no-pb-sm {
	@include sm {
		padding-bottom: 0; } }

.section_pt-no-xs {
	@include xs {
		padding-top: 0; } }
