.player {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	min-width: 320px;
	z-index: 99;
	background: #0f1d2b;
	&__video {
		position: relative;
		height: 100%;
		overflow: hidden;
		background: #000;
		display: flex;
		video {
			width: 100%;
			height: 100%;
			max-width: 100%;
			max-height: 100%;
			margin: auto; } }
	&__back {
		position: absolute;
		top: 40px;
		left: 40px;
		z-index: 10;
		@include sm {
			top: 20px;
			left: 20px; } }
	&__bottom {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 2;
		background: #0f1d2b; }
	&__controls {
		display: flex;
		margin-top: auto;
		@include sm {
			flex-direction: column; } }
	&__btns-wrap {
		flex: 0 0 200px;
		display: flex;
		border-right: 1px solid #000;
		@include sm {
			flex: auto; } }
	&__btns {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin: auto;
		@include sm {
			justify-content: space-between;
			margin: 0 auto;
			padding: 10px 20px;
			width: 100%;
			max-width: 320px; } }
	&__btn {
		padding: 10px;
		@include sm {
			padding: 0; } }
	&__body {
		flex: 1;
		font-size: 14px;
		@include pop;
		font-weight: 300; }
	&__top {
		min-width: 60px;
		padding: 10px 38px;
		background: #0c1722;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include sm {
			padding-left: 20px;
			padding-right: 20px; } }
	&__time {
		color: #9fa4aa; }
	&__schedule {
		padding: 30px 38px;
		@include sm {
			padding: 10px 60px 10px 20px; } }
	&__channel {
		display: inline-block;
		vertical-align: top;
		margin-right: 30px;
		img {
			max-width: 100%; }
		@include md {
			margin-right: 15px; }
		@include sm {
			width: 42px;
			vertical-align: middle; } }
	&__list {
		display: inline-block;
		vertical-align: top;
		@include sm {
			max-width: calc(100% - 65px);
			vertical-align: middle; } }
	&__programm {
		position: relative;
		color: #9fa4aa;
		@include sm {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis; }
		a {
			color: inherit; }
		&:not(:last-child) {
			padding-bottom: 10px;
			margin-bottom: 10px;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				width: 20px;
				height: 2px;
				background: #9fa4aa; } }
		&.is-active {
			font-weight: 500;
			color: #fff;
			font-size: 18px;
			@include sm {
				font-size: 14px; } }
		span {
			display: inline-block;
			margin-right: 30px;
			font-weight: 300;
			font-size: 14px;
			color: #9fa4aa;
			letter-spacing: .06em;
			@include sm {
				margin-right: 15px; } } }
	&__label {
		display: inline-block;
		margin-right: 30px;
		@include md {
			margin-right: 15px; }
		@include sm {
			display: block;
			margin-bottom: 7px; } }
	&__arrows {
		flex: 0 0 113px;
		border-left: 1px solid #000;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		padding: 15px 10px;
		@include from-sm {
			flex: 0 0 113px; }
		@include sm {
			position: absolute;
			right: 0;
			bottom: 0;
			height: 74px;
			padding-top: 5px;
			padding-bottom: 5px;
			.btn-arr {
				width: 30px;
				height: 30px; }
			.btn-arr_top {
				i {
					border-width: 0 12px 15px; } }
			.btn-arr_bottom {
				i {
					border-width: 15px 12px 0; } } } }
	&__additional {
		@include sm {
			.btn + .btn {
				margin-left: 10px; } } } }

