.footer {
	position: relative;
	background: #000;
	padding: 25px 0;
	@include sm {
		padding: 20px 0; }
	> .container {
		@include sm {
			display: flex;
			flex-direction: column;
			justify-content: center; } }
	&__left {
		@include from-sm {
			float: left; }
		@include sm {
			order: 1;
			text-align: center; } }
	&__right {
		@include from-sm {
			float: right;
			padding-top: 2px; }
		@include sm {
			order: 3;
			text-align: center; } }
	&__center {
		@include sm {
			order: 2;
			margin: 20px 0; }
		@include from-sm {
			padding: 0 170px 0 100px;
			@include md {
				padding-right: 90px;
				padding-left: 90px; } } }

	&__info {
		display: flex;
		justify-content: center;
		align-items: center;
		@include md {
			flex-direction: column; } }
	&__text {
		margin-right: 40px;
		color: #949494;
		font-size: 11px;
		text-align: right;
		@include pop;
		@include md {
			@include from-sm {
				margin-right: 15px; } }
		@include sm {
			text-align: center;
			margin-right: 0; }
		a {
			color: #fff;
			&:hover {
				text-decoration: underline; } }
		a[href^=tel] {
			color: inherit; }
		p {
			margin-bottom: 5px; } }
	&__list {
		text-align: right;
		li {
			&:not(:last-child) {
				&:after {
					content: ' - '; } } }
		a {
			color: #fff;
			&:hover {
				text-decoration: underline; } }
		li {
			display: inline-block; } }
	&__soc {
		@include sm {
			order: -1;
			margin-bottom: 20px; } }
	&__mc {
		@include sm {
			width: 60px;
			height: auto; } } }

