.block {
	display: flex;
	@include md {
		display: block; }
	&__gallery {
		flex: 1 0 563px;
		margin-right: 55px;
		@include lg {
			flex: 1 0 500px;
			margin-right: 30px; }
		@include md {
			max-width: 600px;
			margin: 0 auto 20px; }
		@include sm {
			margin-bottom: 30px; } }

	&__info {
		flex: 100%;
		@include md {
			max-width: 600px;
			margin: 0 auto; } }
	&__tab {
		&:not(:first-child) {
			display: none; } } }
