.movie {
	@include from-sm {
		display: flex; }
	&__side {
		position: relative;
		flex: 0 0 477px;
		width: 477px;
		margin-right: 35px;
		@include md {
			width: 377px;
			flex: 0 0 377px; }
		@include sm {
			width: auto;
			flex: auto;
			margin: 0 -30px 30px; } }
	&__links {
		padding: 30px 10px 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include from-sm {
			position: absolute;
			top: 100%;
			width: 100%; }
		@include sm {
			padding-top: 15px; }
		a {
			color: #fff;
			font-size: 15px;
			transition: color .3s;
			letter-spacing: .06em;
			@include sm {
				font-size: 13px; }
			&:first-child {
				.icon {
					margin-right: 10px;
					@include xs {
						margin-right: 5px; } } }
			&:last-child {
				.icon {
					margin-left: 10px;
					@include xs {
						margin-left: 5px; } } }
			&:hover {
				color: $c-blue; }
			.icon {
				fill: currentColor;
				font-size: 8px; } } }
	&__image {
		height: 320px;
		background-size: cover;
		background-position: center;
		@include from-sm {
			border-radius: 4px; }
		@include md {
			height: 280px; }
		@include sm {
			width: 100%;
			height: 0;
			padding-bottom: 56.25%; } }
	&__info {
		flex: 1 1 auto;
		display: flex;
		flex-direction: column; }
	&__footer {
		margin-top: auto;
		@include sm {
			margin-top: 10px; } }
	&__footer-text {
		text-transform: uppercase;
		@include pop;
		font-weight: 300;
		margin-bottom: 15px; } }

.movie_links {
	@include from-sm {
		padding-bottom: 50px; } }
