.chapter {
	margin: 0 -10px;
	@include md {
		// margin-right: -20px
		margin: 0; }
	@include sm {
		// margin-right: -30px
		margin: 0; }
	&:not(:last-child) {
		margin-bottom: 62px;
		@include sm {
			margin-bottom: 15px; } }
	&__gallery {
		&:not(.slick-initialized) {
			white-space: nowrap;
			@include from-md {
				overflow: hidden; }
			@include md {
				padding-bottom: 20px;
				margin: 0 -20px; }
			@include xs {
				margin: 0 -30px; }
			.chapter__slide {
				padding: 0 15px;
				@include md {
					&:first-child {
						padding-left: 20px; }
					&:last-child {
						padding-right: 20px; } }
				@include sm {
					padding: 0 10px;
					&:first-child {
						padding-left: 30px; }
					&:last-child {
						padding-right: 30px; } } }
			.item {
				white-space: normal; } }
		@include md {
			overflow: auto;
			margin-left: 10px; } }
	.slick-slide {
		> div {
			margin: 0 10px; }
		@include r(1100); }
	.slick-arrow {
		position: absolute;
		width: 45px;
		height: 45px;
		top: 85px;
		z-index: 2;
		transition: opacity .3s;
		&.slick-disabled {
			opacity: 0; } }
	.slick-prev {
		left: -13px;
		background-image: url(data:image/svg+xml;base64,); }
	.slick-next {
		right: -15px;
		background-image: url(data:image/svg+xml;base64,); }
	&__title {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		margin: 0 10px 30px;
		// margin-bottom: 30px
		@include md {
			margin-left: 0;
			margin-right: 0;
 }			// margin-right: 20px
		@include sm {
			// margin-right: 30px
 } }			// margin-bottom: 20px
	&__change {
		line-height: 1.4; }
	&__slide {
		display: inline-block;
		vertical-align: top;
		.item {
			min-width: 285px;
			@include r(1280) {
				min-width: 230px; }
			@include md {
				min-width: 0;
				width: 210px; }
			@include sm {
				width: 193px; } } } }

.chapter_sm {
	&:not(:last-child) {
		margin-bottom: 50px; } }

.item {
	$this: &;

	display: block;
	padding-bottom: 5px;
	&.is-removable {
		#{$this}__delete {
			display: flex; } }
	&__top {
		position: relative;
		height: 210px;
		margin-bottom: 20px;
		border-radius: 4px;
		overflow: hidden;
		@include sm {
			height: 147px; } }
	&__img {
		display: block;
		height: 100%;
		background-size: cover;
		background-position: center;
		&:hover {
			#{$this} {
				&__play {
					opacity: 1; } } } }
	&__play {
		border-radius: 0; }
	&__name {
		display: inline;
		color: #fff;
		@include pop;
		font-size: 16px;
		transition: color .3s;
		cursor: pointer;
		&:after {
			content: '>';
			color: inherit;
			display: inline-block;
			vertical-align: middle;
			position: relative;
			top: 1px;
			width: 10px;
			margin-left: 10px; }
		&:hover {
			color: $c-blue;
			span {
				text-decoration: underline; } }
		span {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			max-width: 90%;
			max-width: calc(100% - 25px);
			color: inherit;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis; } }
	&__logo {
		position: absolute;
		top: 20px;
		right: 20px;
		width: 100px;
		z-index: 1;
		img {
			max-width: 100%;
			height: auto; } }
	&__info {
		display: flex;
		align-items: center;
		min-height: 93px;
		margin-top: 20px;
		padding: 14px 30px;
		border-radius: 3px;
		background: #000;
		border: 1px solid #081c26; }
	&__text {
		@include pop;
		font-size: 12px;
		font-weight: 300;
		color: #99999a;
		line-height: 1.4;
		p {
			&:not(:last-child) {
				margin-bottom: 7px; } }
		strong {
			display: block;
			font-size: 18px;
			font-weight: 500;
			color: #fff;
			letter-spacing: 0.06em;
			line-height: 1;
			margin-bottom: 7px; } }
	&__delete {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: none;
		align-items: center;
		justify-content: center;
		background: rgba(#0F1D2B , .9);
		z-index: 99;
		@include gpu;
		.icon {
			fill: #fff;
			font-size: 40px; } } }

.item_mod {
	.item {
		&__img {
			&:hover {
				&:after {
					opacity: 0; } }
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: inherit;
				background: rgba(#143D45, .4);
				transition: opacity .3s; } } } }

.c-item {
	&__title {
		margin-bottom: 30px; } }




